.sec-two {
  background-color: var(--box-color);
  display: flex;
  align-items: center;
  justify-content: center;
}

.sec-two__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1200px;
}

.container__img {
  background-image: url(../../img/banner.png);
  background-size: cover;
  background-position-x: 35%;
  border: 1.2rem solid var(--tertiary-color);
  border-radius: 50%;
}

.container__img > figure {
  width: 25rem;
  height: 25rem;
}

.container__text {
  margin-left: 5rem;
  color: #fff;
}

.text__title 
{
  font-size: 2.25em;
  text-transform: uppercase;
  margin-bottom: 1rem;
}